/* eslint-disable */
<template lang="html">
  <el-row class="main">
    <el-card class="login__box">
      <div slot="header" class="login__header">
        <span> <i class="fa fa-sign-in"></i>  </span>
      </div>
      <el-form :model="loginForm">
        <el-form-item>
          <el-input v-model="loginForm.name" prefix-icon="fa fa-user fa-lg" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-input type="password" v-model="loginForm.password"  prefix-icon="fa fa-lock fa-lg" placeholder=""></el-input>
        </el-form-item>
        <!-- <el-form-item prop="type">
          <el-checkbox label="" v-model="formLabelAlign.type"></el-checkbox>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" class="login__button" @click="login()"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </el-row>
</template>

<script type="text/javascript">
  import router from '../../router/router'
  import axios from 'axios'
  export default {
    //  name 
    name: 'RangeSlider',
    // 
    extends: {},
    // 
    data () {
      return {
        loginForm: {
          name: '',
          password:'',
        },
      }
    },
    computed: {},
    // 
    components: {},
    //  
    watch: {},
    methods: {
      login () {
        //
        if(!this.loginForm.name || !this.loginForm.password){
          this.$message.warning('');
          return false;
        }
        this.$axios({
          method: 'POST',
          url: `${API_HOST}Admin/Login`,
          data: {
            name: this.loginForm.name,
            password: this.loginForm.password
          }
        }).then((res) => {
          if(!res.data.status) {
            this.$message.error(res.data.msg)
          } else {
            sessionStorage.setItem('adminName',this.loginForm.name)
            this.$message.success(res.data.msg)
            this.$router.push({path:'/admin/index'})
          }
        }).catch((err) => {
          this.$message.error(` ${err}`)
        })
/*        if(this.formLabelAlign.type === true){
          console.log(this.formLabelAlign.type);
          //3
          this.setCookie(this.formLabelAlign.name, this.formLabelAlign.region, 7); 
        }else{
          console.log("Cookie");
          //Cookie
          this.clearCookie();
        }
        //
          axios.post(API_HOST+'Admin/Login',{
            name:this.formLabelAlign.name,
            password:this.formLabelAlign.region
          })
          .then(function(res){
            console.log(res);
            if(res.status = true){
              localStorage.adminName = this.loginForm.name;
              alert("");
              router.push({ path: '/admin/index' });
            }
          })
          .catch(function(err){
            console.log(err);
            alert("");
          }); */
      },
        //cookie
      setCookie(c_name, c_region, exdays){
        var exdate = new Date(); //
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
        //cookie
        window.document.cookie = "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
        window.document.cookie = "userPwd" + "=" + c_region + ";path=/;expires=" + exdate.toGMTString();
      },
      //cookie
      getCookie: function() {
          if (document.cookie.length > 0) {
              var arr = document.cookie.split('; '); //
              for (var i = 0; i < arr.length; i++) {
                  var arr2 = arr[i].split('='); //
                  //
                  if (arr2[0] == 'userName') {
                      this.formLabelAlign.name = arr2[1]; //
                  } else if (arr2[0] == 'userPwd') {
                      this.formLabelAlign.region = arr2[1];
                  }
                  this.formLabelAlign.type = true;
              }
          }
      },
      //cookie
      clearCookie: function() {
          this.setCookie("", "", -1); //21
      }
    },
    // 
    beforeCreate () {},
    //cookie
    mounted () {
      this.getCookie();
    }
  }
</script>

<style scoped lang="scss">
  .main {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../../assets/login_bg.jpg');
    background-size: cover;
    background-position: 50%;
    .login__box {
      position: absolute;
      top: 20%;
      // transform: translateY(-56%);
      // -webkit-transform: translateY(-56%);
      right: 70px;
      width: 350px;
      .login__header {
        font-weight: bold;
        text-align: left;
      }
      .login__button {
        width: 100%;
      }
    }
  }
</style>


